<template>
        <el-form :model="LoginForm" ref="LoginForm" :rules="rule" label-width="0" class="login-form"  @keyup.enter.native="onLogin" >
            <h3>用户登录</h3>
            <el-form-item prop="userName">
                <el-input type="text" v-model="LoginForm.userName" placeholder="填写自己的姓名，不支持昵称登录"></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input type="password" v-model="LoginForm.password" placeholder="用户密码"></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="danger" class="submitBtn" round @click.native.prevent="onLogin"  :loading="logining">登录</el-button>
                <el-button type="primary" class="resetBtn" round @click.native.prevent="reset">重置</el-button>
                <hr>
                <!--<p>还没有账号，马上去<span class="to" @click="toregin" >注册</span></p>-->
                <p>还没有账号，马上去联系管理员</p>
            </el-form-item>
        </el-form>
</template>

<script>
    export default {
        // ....
        name: 'userLogin',
        data () {
            return {
                LoginForm: {
                    userName: '',
                    password: ''
                },
                logining: false,
                rule: {
                    userName: [
                        {
                            required: true,
                            max: 14,
                            min: 2,
                            message: '用户名是必须的，长度为2-14位',
                            trigger: 'blur'
                        }
                    ],
                    password: [
                        {
                            required: true,
                            message: '密码是必须的！',
                            trigger: 'blur'
                        }
                    ]
                }
            }
        },
        methods: {
            async r_userLogin(par){
                let R = new this.$http();
                let {data} = await R.post(par, 'OAMP_userLogin');
                if (data != null && data !== 'null') {
                    return data;
                }
                else
                    return null;
            },
            // ...
            onLogin () {
                this.$refs.LoginForm.validate(async (valid) => {
                    let _this = this;
                    if (valid) {
                        _this.logining = true;
                        // console.log('开始请求后台数据，验证返回之类的操作！')
                        // 登录作为参数的用户信息
                        let loginParams = {
                            userName: this.LoginForm.userName,
                            password: this.LoginForm.password
                        };
                        let data = await _this.r_userLogin(loginParams);
                        if (data == null) {
                            /*登录失败*/
                            /*状态切换成重新登录状态*/
                            _this.$message.error('登录名或密码错误')
                            _this.logining = false;
                        } else {
                            /*登录成功 修改登录状态、存储登录信息*/
                            _this.$store.commit('setUserInfo', data);
                            _this.$store.commit('setIsLogin', true);
                            location.reload()
                        }
                    } else {
                        this.$message.error('登录错误');
                    }
                })
            },
            reset () {
                this.$refs.LoginForm.resetFields()
            },
            // toregin () {
            //     this.$router.push('/user/userRegistration')
            // }
        }
    }
</script>

<style scoped>
    .login-form {
        margin: 20px auto;
        width: 310px;
        background: #fff;
        box-shadow: 0 0 35px #B4BCCC;
        padding: 30px 30px 0 30px;
        border-radius: 25px;
    }

    .submitBtn {
        width: 65%;
    }

    .to {
        color: #67C23A;
        cursor: pointer;
    }
</style>



